.blog-section {
  padding-bottom: 104px; }
  .blog-section .title h1 {
    margin: 4px 0 0; }
  .blog-section .blog__items {
    gap: 18px;
    width: 100%;
    margin-top: 52px; }
  .blog-section .blog .blog_item {
    max-width: 376px;
    width: 100%;
    margin: 0; }
  @media (max-width: 1024px) {
    .blog-section {
      padding-bottom: 80px; }
      .blog-section .blog__items {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 24px 12px;
        margin-top: 21px; }
      .blog-section .blog .blog_item {
        max-width: none; } }
  @media (max-width: 828px) {
    .blog-section {
      padding-top: 70px; }
      .blog-section .title h1 {
        margin: 0; } }
  @media (max-width: 720px) {
    .blog-section {
      padding-bottom: 48px; } }
  @media (max-width: 550px) {
    .blog-section {
      padding-bottom: 48px; }
      .blog-section .blog__items {
        grid-template-columns: repeat(1, minmax(0, 1fr)); } }
