.small {
  font-family: "Futura PT Book", sans-serif;
  font-weight: 400; }

.medium {
  font-family: "Futura PT Medium", sans-serif;
  font-weight: 500; }

.bold {
  font-family: "Futura PT Bold", sans-serif;
  font-weight: 700; }

.color_black {
  color: #453f3a; }
  .color_black_sub {
    color: #89817c; }

.color_white {
  color: #fffcf2; }

.color_gold {
  color: #a46c46; }

.font-size-14 {
  font-size: 14px; }

.font-size-18 {
  font-size: 18px; }

.font-size-26 {
  font-size: 26px; }

.font-size-36 {
  font-size: 36px; }

.flex {
  display: flex;
  align-items: center; }

.title {
  display: flex;
  align-items: center; }
  .title h1 {
    font-family: "Futura PT Bold", sans-serif;
    font-weight: 700;
    font-size: 30px;
    line-height: 38px;
    color: #453f3a; }
  .title span {
    margin-left: 20px;
    color: #89817c; }
  @media (max-width: 1024px) {
    .title h1 {
      font-size: 24px;
      line-height: normal; } }
  @media (max-width: 720px) {
    .title {
      flex-direction: column;
      align-items: start;
      justify-content: center; }
      .title h1 {
        font-size: 20px; }
      .title span {
        margin: 4px 0 0; } }

.bread-crumbs {
  padding-top: 15px; }
  @media (max-width: 1024px) {
    .bread-crumbs {
      padding-top: 20px; } }
  @media (max-width: 828px) {
    .bread-crumbs {
      display: none; } }

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }
