.authorization__bottom {
  margin-top: 104px; }

@media (max-width: 1024px) {
  .authorization__bottom {
    margin-top: 80px; } }

@media (max-width: 720px) {
  .authorization__bottom {
    margin-top: 48px; } }

.login-modal__form-field {
  max-width: 320px; }

.login-modal__form-buttons {
  margin-top: 30px; }

.login-modal__sign-in-btn {
  max-width: 265px; }

.login-modal__forgot-password-btn {
  max-width: 164px;
  margin-top: 10px;
  padding: 0 !important;
  color: #89817c;
  line-height: 130%;
  font-weight: 400; }

.login-modal__create-account-btn {
  max-width: 276px;
  margin-top: 20px; }

@media (max-width: 720px) {
  .login-modal__form-field {
    max-width: none; }
  .login-modal__form-buttons {
    margin-top: 24px; }
  .login-modal__sign-in-btn {
    max-width: none; }
  .login-modal__forgot-password-btn {
    margin-top: 22px; }
  .login-modal__create-account-btn {
    max-width: none;
    margin-top: 24px; } }

.register-modal__form {
  max-width: 440px; }
  .register-modal__form-description {
    margin-top: 10px; }
  .register-modal__form-fields {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    column-gap: 20px; }
  .register-modal__form-field.--full {
    grid-column: span 2 / span 2; }
  .register-modal__form-buttons {
    margin-top: 40px; }
  .register-modal__form-info {
    margin-top: 10px;
    color: #212529;
    font-size: 14px;
    line-height: 130%; }
    .register-modal__form-info-text {
      color: #6c757d; }
    .register-modal__form-info a {
      transition: all 0.2s; }
      .register-modal__form-info a:hover {
        color: #a46c46; }

.register-modal__create-account-btn {
  max-width: 276px; }

.register-modal__sign-in-btn {
  max-width: 280px;
  margin-top: 30px; }

@media (max-width: 900px) {
  .register-modal__form {
    max-width: 360px; } }

@media (max-width: 720px) {
  .register-modal__form {
    max-width: none; }
    .register-modal__form-description {
      margin-top: 12px; }
    .register-modal__form-buttons {
      margin-top: 32px; }
    .register-modal__form-info {
      margin-top: 16px; }
      .register-modal__form-info p,
      .register-modal__form-info a,
      .register-modal__form-info span {
        display: inline; }
  .register-modal__create-account-btn {
    max-width: none; }
  .register-modal__sign-in-btn {
    max-width: none;
    margin-top: 24px; } }
