.blog {
  display: flex;
  width: 100%;
  gap: 12px; }
  .blog_item {
    display: flex;
    flex-direction: column;
    transition: all 0.3s;
    width: 32%; }
    .blog_item:hover {
      transform: scale(1.02); }
    .blog_item_img {
      width: 100%; }
      .blog_item_img img {
        object-fit: cover;
        width: 100%;
        height: 230px;
        border-radius: 8px; }
    .blog_item_info {
      padding: 0 8px; }
    .blog_item_title {
      margin: 16px 0 0;
      color: #453f3a;
      font-size: 22px;
      line-height: 130%;
      font-weight: 400; }
    .blog_item_text {
      font-size: 20px;
      line-height: 130%;
      color: #89817c;
      margin-top: 8px; }
  @media (max-width: 1024px) {
    .blog_item_img img {
      height: 188px; }
    .blog_item_info {
      padding: 0 10px; }
    .blog_item_title {
      margin: 12px 0 0;
      font-size: 16px; }
    .blog_item_text {
      font-size: 14px; } }
