.contacts-modal__info {
  max-width: 400px; }
  .contacts-modal__info-details {
    margin-top: 36px; }
    .contacts-modal__info-details-item {
      transition: all 0.25s; }
      .contacts-modal__info-details-item:hover a {
        color: #a46c46; }

.contacts-modal__title {
  font-size: 30px; }

.contacts-modal__description {
  max-width: 356px;
  margin-top: 6px; }

.contacts-modal__phone {
  color: #453f3a;
  font-size: 32px; }

.contacts-modal__address {
  margin-top: 7px;
  color: #89817c;
  font-size: 20px; }

.contacts-modal__inst {
  max-width: 22px;
  max-height: 22px;
  margin-top: 17px; }
  .contacts-modal__inst:hover {
    transform: scale(1.3); }

.contacts-modal__feedback-form {
  max-width: 440px;
  margin-top: 60px; }
  .contacts-modal__feedback-form-field textarea {
    min-height: 41.8px;
    height: 41.8px;
    resize: vertical; }
  .contacts-modal__feedback-form-buttons {
    margin-top: 40px; }
  .contacts-modal__feedback-form-info {
    margin-top: 10px; }
    .contacts-modal__feedback-form-info-text {
      color: #6c757d;
      font-size: 14px;
      line-height: 130%; }

.contacts-modal__send-application-btn {
  max-width: 214px; }

@media (max-width: 720px) {
  .contacts-modal__info {
    max-width: none; }
    .contacts-modal__info-details {
      margin-top: 24px; }
  .contacts-modal__title {
    font-size: 24px; }
  .contacts-modal__description {
    max-width: none;
    margin-top: 12px;
    font-size: 14px; }
  .contacts-modal__phone {
    font-size: 24px; }
  .contacts-modal__address {
    margin-top: 8px; }
  .contacts-modal__inst {
    margin-top: 16px; }
  .contacts-modal__feedback-form {
    max-width: none;
    margin-top: 48px; }
    .contacts-modal__feedback-form-buttons {
      margin-top: 32px; }
    .contacts-modal__feedback-form-info {
      margin-top: 16px; }
  .contacts-modal__send-application-btn {
    max-width: none; } }
